<template>
  <div>
    <div class="preview-image-warp">
      <img class="preview-image"
           :src="previewImage"
           alt=""
           ref="previewImage"
           srcset="">
      <span slot="footer"
            style="margin-left: 100px"
            class="dialog-footer">

        <van-button @click="cancleCut">取消</van-button>
        <van-button @click="rotateImg">旋转</van-button>
        <van-button @click="onUpdataPhoto">确定</van-button>
      </span>
    </div>
  </div>

</template>

<script>
  import 'cropperjs/dist/cropper.css'
  import Cropper from 'cropperjs'

  export default {
    name: "cutImg",
    props: ['imgSrc'],
    data() {
      return {
        dialogVisible: false,
        previewImage: this.imgSrc,
        // previewImage: 'https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimg.jj20.com%2Fup%2Fallimg%2Ftp05%2F19100120461512E-0-lp.jpg&refer=http%3A%2F%2Fimg.jj20.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1640081307&t=c9af066a7487fff840e559483b3c7460\n',
        cropper: null
      }
    },
    mounted() {
      this.onDialogOpend()
    },
    methods: {
      onDialogOpend() {
        // 第一次初始化完成后，如果预览图片发生了变化，裁切器默认不会更新
        // 可以使用裁切器的replace方法，更新数据
        if (this.cropper) {
          this.cropper.replace(this.previewImage)
          return
        }
        // 初始化裁切器
        const image = this.$refs.previewImage
        this.cropper = new Cropper(image, {
          aspectRatio: 1, // 初始化剪切选择框大小
          viewMode: 1, // 不能把剪切框移出图片本身
          autoCropArea: 1,
        })
      },
      onUpdataPhoto() {
        // this.$toast(this.cropper.getCroppedCanvas().toDataURL('image/jpeg'))
        // console.log(this.cropper.getCroppedCanvas().toDataURL('image/jpeg'))
        this.$emit('closePage',this.cropper.getCroppedCanvas().toDataURL('image/jpeg'))
      },
      rotateImg(){
        this.cropper.rotate(45);
      },
      cancleCut(){
        this.$emit('cancleCut')
      }

    }
  }
</script>

<style scoped>
  .preview-image-warp .preview-image {
    display: block;
    max-width: 100%;
    height: 300px;
  }
  .preview-image-warp{
    position: absolute;
    height: 60vh;

    left: 0.1rem;
    right: 0.1rem;
    top: 10vh;

  }


</style>
