<template>
  <div>
    <audio ref="audio" :src="'https://res.qiaomukeji.com/h5pro/202110/ljbgm/bgm_21.mp3'" id="audio" preload="auto"
           autoplay="autoplay"></audio>
    <swiper class="swiper"
            ref="mySwiper"
            @slideChangeTransitionStart="slideChangeTransitionStart"
            :options="swiperOption"
            @slideChange="slideChange"
    >
      <!--class="swiper-no-swiping"-->
      <swiper-slide v-for="(item,index) in swiperImg" :key="index">
        <div :class="activeIndex == index?'img-wrap animate__animated animate__pulse':'img-wrap'">
          <img :src="item" alt="" class="back">
        </div>
      </swiper-slide>

      <swiper-slide>
        <img src="../assets/image/twelve.jpg" alt="" class="back lastBack">
        <!--<div class="img-wrap" style="background: white">-->
        <!--</div>-->
        <div class="uploadPage">
          <getPoster @changeDate="changeDate" ref="getPoster" @startPoster="startPoster"
                     @showCutDialog="showCutDialog"></getPoster>
        </div>
      </swiper-slide>
      <div class="year-scroll" v-show="activeIndex != 11">
        <yearsScroll @changePage="changePage" ref="yearsScroll"></yearsScroll>
      </div>
      <!--<div class="swiper-pagination swiper-pagination-white" slot="pagination"></div>-->
      <div class="swiper-button-prev swiper-button-white" slot="button-prev" v-show="activeIndex != 11"></div>
      <div class="swiper-button-next swiper-button-white" slot="button-next" v-show="activeIndex != 11"></div>
    </swiper>
    <van-calendar v-model="pickerVisible" @confirm="onConfirm" :min-date="new Date('2011-10-10')"/>

    <div class="poster-wrap" v-if="showPoster">
      <van-overlay :show="showPoster">
        <van-icon name="close" class="closeWrap" @click="showPoster = false"/>
        <poster :tempObj="tempObj" ref="posterMain"></poster>
      </van-overlay>

    </div>
    <div v-if="showCut">
      <van-overlay :show="showCut">
        <cutImg :imgSrc="imgSrc" @closePage="closePage" @cancleCut="cancleCut"></cutImg>
      </van-overlay>
    </div>

    <!--每一项音频播放-->
    <audio ref="audioItem" :src="itemSrc" id="audioItem"  v-if="itemSrc"></audio>

  </div>
</template>

<script>
  import cutImg from './cutImg'
  import yearsScroll from './yearsScroll'
  import poster from './poster'
  import {swiper, swiperSlide} from "vue-awesome-swiper";
  import "swiper/dist/css/swiper.css";
  import getPoster from './getPoster'

  export default {
    name: "page",
    components: {
      swiper,
      swiperSlide,
      getPoster,
      poster,
      yearsScroll,
      cutImg
    },
    data() {
      return {
        swiperImg:[
          require('../assets/image/one.jpg'),
          require('../assets/image/two.jpg'),
          require('../assets/image/three.jpg'),
          require('../assets/image/four.jpg'),
          require('../assets/image/five.jpg'),
          require('../assets/image/six.jpg'),
          require('../assets/image/seven.jpg'),
          require('../assets/image/eight.jpg'),
          require('../assets/image/nine.jpg'),
          require('../assets/image/ten.jpg'),
          require('../assets/image/eleven.jpg'),
        ],
        ll: require('../assets/image/home_17.jpg'),
        itemSrc: '',
        imgSrc: '',
        showCut: false,
        showPoster: false,
        tempObj: '',
        activeIndex: '',
        pickerVisible: false,
        swiperOption: {
          direction: 'vertical',
          speed: 300,
          effect: 'fade',
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }

        }
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.audioAutoPlay('audio')

      })

      // this.togglePlay()
    },
    methods: {
      closePage(url) {
        if(url.indexOf('data:image/jpeg;base64,')>-1){
          this.showCut = false
          this.imgSrc = url
          this.$refs.getPoster.imgSrc = url
        }else{
          this.$toast('上传不符合规定')
          this.cancleCut()
        }

      },
      cancleCut(){
        this.showCut = false
        this.imgSrc = ''
        this.$refs.getPoster.imgSrc = ''
      },
      showCutDialog(src) {
        this.showCut = true
        this.imgSrc = src
      },
      slideChangeTransitionStart() {
        this.activeIndex = this.$refs.mySwiper.swiper.activeIndex
      },
      slideChange() {
        this.activeIndex = this.$refs.mySwiper.swiper.activeIndex
        this.$nextTick(()=>{
          this.$refs.yearsScroll.action(this.activeIndex)
        })

        if (this.activeIndex == 1) {
          this.itemSrc = "https://res.qiaomukeji.com/h5pro/202110/lj1/m1.mp3?v=24"
          this.$nextTick(() => {
            let audio = document.getElementById('audioItem');
            audio.play()
          })
        } else if (this.activeIndex == 2) {
          this.itemSrc = "https://res.qiaomukeji.com/h5pro/202110/ljabm/p2.mp3?v=24"
          this.$nextTick(() => {
            let audio = document.getElementById('audioItem');
            audio.play()
          })
        } else if (this.activeIndex == 3) {
          this.itemSrc = "https://res.qiaomukeji.com/h5pro/202110/ljabm/p2.mp3?v=24"
          this.$nextTick(() => {
            let audio = document.getElementById('audioItem');
            audio.play()
          })
        } else {
          this.itemSrc = ''
        }
      },
      clickbtn() {
        this.$refs.audio.play()
        // let player = this.$refs.player
        // player.play()
      },
      audioAutoPlay(id) {
        var audio = document.getElementById(id);
        // audio.play();
        document.addEventListener("WeixinJSBridgeReady", function () {
          audio.play();
        }, false);
        document.addEventListener("YixinJSBridgeReady", function () {
          audio.play();
        }, false);
      },
      changeDate() {
        this.pickerVisible = true
      },
      formatDate(date) {
        return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
      },
      onConfirm(date) {
        this.pickerVisible = false;
        this.$refs.getPoster.missDate = this.formatDate(date)
      },
      startPoster(obj) {
        this.tempObj = obj
        this.showPoster = true
        this.$nextTick(() => {
          this.$refs.posterMain.getResult()

        })
      },
      changePage(index) {
        this.activeIndex = index
        this.$refs.mySwiper.swiper.slideTo(index, 50, false);//切换到第一个slide，速度为1秒
      }
    }
  }
</script>

<style scoped>
  .swiper {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

  }

  .swiper-slide {
    height: 100%;
    width: 100%;
    position: relative;
  }

  .uploadPage {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
  }
  .img-wrap {
    width: 100%;
    height: 100%;
  }

  .back {
    overflow: hidden;
    transform: rotate(90deg) translate(calc(100vh / 2 - 100vw / 2), calc(100vh / 2 - 100vw / 2));
    width: 100vh;
    height: 100vw;
  }
  .lastBack{
  }

  .swiper-button-prev {
    top: 5%;
    left: 50%;
    margin: 0;
    margin-left: -10px;
    transform: rotate(90deg);

  }

  .swiper-button-next {
    top: unset;
    bottom: 5%;
    right: 50%;
    margin: 0;
    margin-right: -10px;
    transform: rotate(90deg);
  }

  .poster-wrap {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 99999;
  }

  .closeWrap {
    position: absolute;
    right: 10px;
    top: 30px;
    z-index: 999;
    color: #fff;
    font-size: 0.6rem;
  }

  .swiper-container-vertical > .swiper-pagination-bullets {
    left: 10px;
  }

  .year-scroll {
    position: fixed;
    left: 10px;
    top: 30px;
    width: 60px;
    z-index: 999999;
  }

  .cutImg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99;
  }

</style>
